import React from "react"
import { Typography } from "@mui/material"
import {
  OutcomeContainer,
  Outcome,
  TextSection,
  OutcomeList,
  SubOutcomeList
} from "./styled"
import { BasicCarousel } from "@components/Carousel"

interface ComponentProps {
  title?: string
  outcomes?: Array<{
    id?: number
    content?: string
    subcontent?: string[]
  }>
  outcomeImg?: Array<{
    image: string
    title: string
    description: string
  }>
}

const Component: React.FC<ComponentProps> = ({
  title = "",
  outcomes = [],
  outcomeImg = []
}) => {
  return (
    <OutcomeContainer>
      <Outcome>
        <div className="container slide-container">
          <BasicCarousel height={200} perPage={3} gap={8} slides={outcomeImg} />
        </div>
        <div className="container">
          <TextSection>
            {title && <Typography variant="h1Bold">{title}</Typography>}
            <OutcomeList>
              {outcomes.map((outcome, index) => (
                <>
                  <Typography variant="smallBody">
                    {index + 1}. {outcome.content}
                  </Typography>
                  {outcome.subcontent && (
                    <SubOutcomeList>
                      {outcome.subcontent.map((sub, subIndex) => (
                        <SubOutcomeList>
                          <Typography variant="smallBody">
                            {subIndex + 1}. {sub}
                          </Typography>
                        </SubOutcomeList>
                      ))}
                    </SubOutcomeList>
                  )}
                </>
              ))}
            </OutcomeList>
          </TextSection>
        </div>
      </Outcome>
    </OutcomeContainer>
  )
}

export default Component
